/* DIALOGS */
ion-alert {
  .alert-wrapper {
    .alert-head {
      background-color: var(--ion-color-primary);

      h2 {
        font-weight: 300;
        font-size: 20px;
        text-align: center;
        color: var(--ion-color-primary-contrast);
        margin-top: 6px;
        margin-bottom: 6px;
      }
    }

    .alert-button-group {
      border-top: 1px solid var(--ion-item-border-color, var(--ion-border-color, var(--ion-color-step-150, rgba(0, 0, 0, .13))));

      .alert-button {

        span {
          color: var(--ion-color-primary);
        }

        &.alert-button-logout,
        &.cancel-button {

          span {
            color: var(--ion-color-medium-shade) !important;
          }
        }
      }
    }

    .alert-radio-group {

      .alert-radio-button {
        height: auto;
        contain: content;

        .alert-button-inner {
          .alert-radio-label {
            white-space: normal;
          }
        }
      }
    }

    .alert-message {
      padding-top: 24px !important;
      font-size: 14px;
      background: var(--ion-color-primary-contrast);

      &:empty {
        padding: 0px !important;
      }
    }

  }

  &.error-dialog {
    .alert-wrapper {
      .alert-head {
        background-color: var(--ion-color-danger);
      }

      .alert-button-group {
        .alert-button {
          span {
            color: var(--ion-color-danger);
          }
        }
      }
    }
  }

  &.success-dialog {
    .alert-wrapper {
      .alert-head {
        background-color: var(--ion-color-success);
      }

      .alert-button-group {
        .alert-button {
          span {
            color: var(--ion-color-success);
          }
        }
      }
    }
  }

}