  }

  .transition-button fa-icon {
    padding-right: 0.3em;
    text-transform: none !important;
    font-size: large;
    font-weight: bold;
  }

  .transition-button {
    font-size: large;
    font-weight: bold;
  }

  .transitions-buttons-text {
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 0.75em;
  }

  ion-button > fa-icon {
    padding-right: 0.2em;
    font-size: medium;
  }

  .one-transition-footer {
    min-height: 10%;
  }

  .one-type-multiple-transition-footer {
    min-height: 17%;
  }

  .two-type-multiple-transition-footer {
    min-height: 25%;
  }

  .update-cloud {
    color: var(--ion-color-success);
  }

  .route-optimization-popover{
    .popover-content {
      left: 0;
      --min-width: fit-content;
    }
    --backdrop-opacity: var(--ion-backdrop-opacity, 0.5);
  }
}

